import {Component, OnInit} from "@angular/core";
import {PermissionService} from "@generated/permission-service";
import {Privileges} from "@generated/privileges";
import {VumBaselineRecommendationService} from "@generated/vum-baseline-recommendation-service";
import {VumBaselineRecommendationType} from "@generated/vum-baseline-recommendation-type";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {LoaderName} from "@service/flow/loader-name";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {RefreshHandler} from "@service/global-refresh.service";
import {ManagedObject} from "@service/managed-object";
import {SrMessageType} from "@service/screen-reader-announcer.service";
import {TaskMonitorService} from "@service/task-monitor-service";
import {ModalBuilder} from "@util/modal-builder";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "vum-cluster-settings.html",
   styleUrls: ["vum-cluster-settings.scss"],
   selector: "vum-cluster-settings",
})
export class VumClusterSettingsComponent implements OnInit {

   baselineRecommendation: string;
   hasEditPrivilege: boolean;
   clusterRef: ManagedObject;

   @ErrorMessage({
      name: [LoaderName.ON_INIT, LoaderName.PERFORM_CHANGE],
      msg: "vsan.common.error.dataExtraction",
   })
   alertMessage: string;

   @LoadingIndicator({name: LoaderName.ON_INIT})
   loading: boolean;

   @LoadingIndicator({name: LoaderName.PERFORM_CHANGE})
   loadingTaskResult: boolean;

   constructor(private permissionService: PermissionService,
         private taskSvc: TaskMonitorService,
         private vumBaselineService: VumBaselineRecommendationService) {
   }

   @Loader({
      name: LoaderName.ON_INIT,
      srMessage: "vsan.cluster.updates.clusterSettings.title",
      srMessageType: SrMessageType.PAGE_TITLE,
   })
   ngOnInit(): void {
      this.clusterRef = ManagedObject.contextObject;
      this.fetch();
   }

   @RefreshHandler
   async fetch(): Promise<void> {
      const fetchPermissionPromise: Promise<boolean> = this.permissionService.hasPermissions(this.clusterRef,
            [Privileges.EDIT_CLUSTER()])
            .then(res => this.hasEditPrivilege = res);

      await Promise.all([fetchPermissionPromise, this.fetchBaselineRecommendation()]);
   }

   private async fetchBaselineRecommendation(): Promise<void> {
      const baselineRecommendationType: VumBaselineRecommendationType =
            await this.vumBaselineService.getClusterRecommendation(this.clusterRef);
      this.baselineRecommendation = this.getBaselineRecommendationLabel(baselineRecommendationType);
   }

   private getBaselineRecommendationLabel(recommendation: VumBaselineRecommendationType): string {
      switch (recommendation) {
         case VumBaselineRecommendationType.latestRelease:
            return VsanUiUtils.getString("vsan.vum.baseline.recommendation.option.latest.release.label");
         case VumBaselineRecommendationType.latestPatch:
            return VsanUiUtils.getString("vsan.vum.baseline.recommendation.option.latest.patch.label");
         case VumBaselineRecommendationType.noRecommendation:
            return VsanUiUtils.getString("vsan.vum.baseline.recommendation.option.no.recommendation.label");
         default:
            throw new Error(`Not supported vum baseline recommendation found: ${recommendation}`);
      }
   }

   edit(): void {
      if (!this.hasEditPrivilege) {
         return;
      }

      new ModalBuilder("vum_clusterSettings/vumBaselineRecommendation")
            .setSize(650, 480)
            .open(this.clusterRef)
            .then((taskRef: ManagedObject) => this.onBaselineRecommendationChange(taskRef));
   }

   @Loader({name: LoaderName.PERFORM_CHANGE})
   private async onBaselineRecommendationChange(taskRef: ManagedObject): Promise<void> {
      await this.taskSvc.getAwaitResult(taskRef);
      await this.fetchBaselineRecommendation();
   }
}
