import {Component, OnInit} from "@angular/core";
import {BaselineRecommendationData} from "@generated/baseline-recommendation-data";
import {VumBaselineRecommendationService} from "@generated/vum-baseline-recommendation-service";
import {VumBaselineRecommendationType} from "@generated/vum-baseline-recommendation-type";
import {ClusterComputeResource} from "@service/client/dataservice/data-service-property";
import {DataServiceClient} from "@service/client/dataservice/data-service.service";
import {ManagedObject, ManagedObjectUtils} from "@service/managed-object";
import {Logger} from "@util/logger";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "vum-baseline-recommendation.html",
   selector: "vum-baseline-recommendation",
   styleUrls: ["vum-baseline-recommendation.scss"],
})
export class VumBaselineRecommendationComponent implements OnInit {
   defaultRecommendation: VumBaselineRecommendationType = VumBaselineRecommendationType.latestRelease;
   selectedRecommendation: VumBaselineRecommendationType = VumBaselineRecommendationType.latestRelease;

   loading: boolean;
   alertMessages: string[] = [];
   clusterName: string;
   VumBaselineRecommendationType = VumBaselineRecommendationType;

   constructor(private recommendationService: VumBaselineRecommendationService,
         private dataService: DataServiceClient) {
   }

   ngOnInit() {
      const currentContextObj: ManagedObject = ManagedObject.contextObject;
      let vcRef: ManagedObject = null;
      let clusterRef: ManagedObject = null;

      // This dialog should be used at cluster or vc level, the context object should be cluster or vc
      if (this.isVcRoot()) {
         vcRef = currentContextObj;
         clusterRef = null;
      } else if (currentContextObj.type === ManagedObjectUtils.CLUSTER) {
         vcRef = ManagedObject.vcRoot(currentContextObj.serverGuid);
         clusterRef = currentContextObj;
      }

      if (vcRef === null) {
         throw new Error("Unsupported context object, only cluster or VC is supported.");
      }

      this.loading = true;
      if (clusterRef) {
         this.dataService
               .getProperty(ClusterComputeResource.name, clusterRef)
               .then(name => this.clusterName = name);
      }

      this.recommendationService.getVumBaselineRecommendation(vcRef, clusterRef)
            .then((data: BaselineRecommendationData) => {
               this.loading = false;
               if (data.vcRecommendation) {
                  this.defaultRecommendation = data.vcRecommendation;
               }

               if (data.clusterRecommendation) {
                  this.selectedRecommendation = data.clusterRecommendation;
               }
            })
            .catch(this.handleError);
   }

   private isVcRoot = (): boolean => {
      return ManagedObjectUtils.isVcRoot(ManagedObject.contextObjectUid);
   };

   private handleError = () => {
      this.loading = false;
      this.alertMessages = [VsanUiUtils.getString("vsan.vum.baseline.recommendation.error")];
   };

   confirm = () => {
      this.loading = true;
      const currentContextObj: ManagedObject = ManagedObject.contextObject;
      let promise: Promise<ManagedObject> = null;
      if (currentContextObj.type === ManagedObjectUtils.CLUSTER) {
         promise = this.recommendationService.setClusterVumBaselineRecommendation(currentContextObj,
               this.selectedRecommendation.toString());
      }

      if (promise) {
         promise.then((task: ManagedObject) => {
            this.loading = false;
            VsanUiUtils.closeModalDialog(task);
         }).catch(this.handleError);
      } else {
         Logger.error("Setting is not saved because of unsupported context object, only cluster or VC is supported.");
      }
   };

   get dialogTitle(): string {
      if (this.isVcRoot()) {
         return VsanUiUtils.getString("vsan.vum.baseline.recommendation.vc.dlg.title");
      }
      return VsanUiUtils.getString("vsan.vum.baseline.recommendation.cluster.dlg.title");
   }
}
